<template>
    <nav>
        <ul>
            <li>
                <router-link to="/" exact>总览</router-link>
                <router-link to="/add" exact>添加</router-link>
            </li>
            <!-- <li><router-link to="/add">添加</router-link></li> -->
        </ul>
    </nav>
</template>

<script>
export default {
    name:'BlogHeader'
};
</script>

<style scoped>
nav{
    background: crimson;
    padding:30px 0;
    margin-bottom:40px;
}
ul{
    list-style-type:none;
    text-align: center;
    margin:0;
}
li{
    display: inline-block;
    margin:0 10px;
}
a{
    color:#fff;
    text-decoration:none;
    padding:12px;
    border-radius:5px;

}
.router-link-active{
    background:rgba(255,255,255,0.8);
    color:#333;
}
</style>





