<template>
    <div class="singleblog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者：{{blog.author}}</p>
        <p>分类</p>
        <ul>
            <li v-for="item in blog.categories">{{item}}</li>
        </ul>
        <button @click='del'>删除</button>
        <button @click='edit'>编辑</button>
    </div>
</template>

<script>
  import axios from 'axios'

export default {
    name:'SingleBlog',
    data:function(){
        return {
            id:this.$route.params.id,
            blog:{}
        }
    },
    created(){
        // this.$http.get('https://myblog-ce34e.firebaseio.com/post/' + this.id + ".json")
        axios.get('/post/' + this.id + ".json")
            .then(data=>{
                console.log(data)
                this.blog = data.data;
            })
    },
    methods:{
        del(){
            // this.$http.delete('https://myblog-ce34e.firebaseio.com/post/' + this.id + ".json")
            // this.$http.delete('https://wd3780466709glsyyc.wilddogio.com/post/' + this.id + ".json")
            axios.delete('/post/' + this.id + ".json")
                .then(res => {
                    console.log(res)
                    this.$router.push({path:'/'})
                })
        },
        edit(){
            this.$router.push({path:'/edit/'+this.id});
        }
    }
}
</script>
<style scoped>
.singleblog{
    max-width:960px;
    margin:0 auto;
    padding:20px;
    background: #eee;
    border:1px dotted #aaa;
}
</style>




