<template>
    <div class="showblog" v-theme:column="'width'">
        <h1>博客总览</h1>
        <!-- 搜索框 -->
        <input type="text" placeholder="搜索" v-model="search">
        <!-- <div v-for="blog in blogs" class="singleblog"> -->
        <div v-for="blog in filterdBlogs" class="singleblog">
            <router-link :to="'/blog/' + blog.id">
                <h2 v-rainbow>{{blog.title | toUppercase}}</h2>
            </router-link>
            <article>
                {{blog.content}}
            </article>
        </div>
    </div>
</template>

<script>
import axios from 'axios'

export default {
  name: "ShowBlog",
  data: function() {
    return {
      blogs: [],
      search: ""
    };
  },
  computed: {
    filterdBlogs: function() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search);
      });
    }
  },
  created() {
    // this.$http.get('https://jsonplaceholder.typicode.com/posts')
    // this.$http.get("../../static/posts.json") //posts.json必须放在static里面
    // this.$http
      // .get("https://myblog-ce34e.firebaseio.com/post.json")
      // .get("https://wd5259196195tskwvr.wilddogio.com/post.json")
      axios.get("/post.json")
      .then(response => {
        // console.log(response.json());
        console.log(response)
        for (let key in response.data) {
          console.log(key)
          response.data[key].id = key;
          this.blogs.push(response.data[key]);
        }
      });
  }
};
</script>

<style>
.showblog {
  max-width: 800px;
  margin: 0 auto;
}
.singleblog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
  border: 1px dotted #aaa;
}
.showblog a {
  color: #444;
  text-decoration: none;
}

input[type="text"] {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
</style>





