<template>
    <div class="addblog">
        <h2>编辑博客</h2>
        <form v-if="!submmited">
            <label>博客标题</label>
            <input type="text" v-model="blog.title" required />

            <label>博客内容</label>
            <textarea v-model="blog.content"></textarea>

            <div id="checkboxes">
                <label>Vue.js</label>
                <input type="checkbox" value="Vue.js" v-model="blog.categories">
                <label>Node.js</label>
                <input type="checkbox" value="Node.js" v-model="blog.categories">
                <label>React.js</label>
                <input type="checkbox" value="React.js" v-model="blog.categories">
                <label>Angular4</label>
                <input type="checkbox" value="Angular4" v-model="blog.categories">
            </div>
            <label>作者</label>
            <select v-model="blog.author">
                <option v-for="author in authors">{{author}}</option>
            </select>
            <button @click.prevent="save">编辑博客</button>
        </form>
        <div  v-else>
            <h3>您的博客发布成功</h3>
        </div>
        <div id="preview">
            <h3>博客总览</h3>
            <p>博客标题：{{blog.title}}</p>
            <p>博客内容</p>
            <p>{{blog.content}}</p>
            <p>博客分类：</p>
            <ul>
                <li v-for="item in blog.categories">{{item}}</li>
            </ul>
            <p>作者 {{blog.author}}</p>

        </div>
    </div>
</template>

<script>
import axios from 'axios'

export default {
  name: "AddBlog",
  data: function() {
    return {
      id: this.$route.params.id,
      blog: {},
      authors: ["张三", "李四", "王五"],
      submmited: false
    };
  },
  methods: {
    save() {
      let data = {
        title: this.blog.title,
        content: this.blog.content,
        userId: 1
      };
      // this.$http.post('http://jsonplaceholder.typicode.com/posts',data)
      // this.$http.put('https://myblog-ce34e.firebaseio.com/post/' + this.id + ".json",this.blog)
    //   this.$http.put("https://wd5259196195tskwvr.wilddogio.com/post/" + this.id + ".json",this.blog
      axios.put("/post/" + this.id + ".json",this.blog)
        .then(response => {
          console.log(response);
          this.submmited = true;
        });
    },
    getlist() {
      // this.$http.get('https://myblog-ce34e.firebaseio.com/post/' + this.id + ".json")
    //   this.$http.get("https://wd5259196195tskwvr.wilddogio.com/post/" + this.id + ".json")
      axios.get("/post/" + this.id + ".json")
        .then(response => {
            console.log('getlist')
          console.log(response);
          this.blog = response.data;
        });
    }
  },
  created() {
      this.getlist();
  }
};
</script>

<style scoped>
.addblog * {
  box-sizing: border-box;
}
.addblog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}
label {
  display: block;
  margin: 20px 0 10px;
}
input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
}
textarea {
  height: 200px;
}
#checkboxes label {
  display: inline-block;
  margin-top: 20px;
}
#checkboxes input {
  display: inline-block;
  margin-right: 10px;
}
button {
  display: block;
  margin: 20px 0;
  background: red;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}
#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}
h3 {
  margin-top: 10px;
}
</style>





